import { notification } from "antd";
import { WarningOutlined } from "@ant-design/icons";

export const textData =
  "Informar a data de incio das frias. Somente ser permitido data de inicio aps 60 dias da data de requisio. ";
export const textDays =
  "Se parcelado, o nmero de dias no poder ser menor que 5 e uma das parcelas deve ser de 14 dias";

export function WarningNotes(description: string) {
  notification.open({
    message: "Ateno Erro no preenchimento",
    description: description,
    icon: <WarningOutlined style={{ color: "#FF0000" }} />,
    style: {
      background: "#bfcff7",
    },
    duration: 5,
  });
}
